import React from "react";
import { useState, useEffect } from "react";
import { Link, useNavigate, useLocation,} from "react-router-dom";
import { v4 as uuidv4 } from 'uuid';


import { getToken, clearToken } from "../frontpage_login_register/tokenStorage";
import AlertDialog from "./shareview";

const BrowserBar = () => {
const [loggedIn, setLoggedIn] = useState(false);
const [returnData, setReturnData] = useState(null);
const [displayShareView, setDisplayShareView] = useState(false);
//const [viewString, setViewString] = useState(""); 
const [link, setLink] = useState('');
const navigate = useNavigate();
const location = useLocation();
const defaultViewCompareString = "0,0,0,0,0,0"; // ohjelma tallentaa nin
const defaultViewCompareString2 = "000000" // databasen default toiminto tallentaa nin
let Token = getToken();

// BROWSERBAR on kokoajan renderityn, valvoo muunmuassa onko kyttj kirjautunut sisn, pysyyk sallituilla sivuilla, ja nytt oikeat napit tilanteen mukaan. 



useEffect(() => {
    if (Token !== null) {
        setLoggedIn(true);
    }
    else {
        setLoggedIn(false);
    }
}
, [Token]);  // tmn hookin tytyy olla riippuvainen getToken() haun tuloksen muuttumisesta


function createLink(viewID){
    return "http://localhost:3000/shared/" + viewID.toString();
  }
  
const handleSaveShareClick = async(event) => {      // Tarkistaa viimeisimmn view:n kyttjlt, sitten tallentaa savedviews tableen tietokantaan.
    let latestViewString = await updateDefaultViewString(); // ensin katotaan view ajantasalle varmasti
    let viewID = uuidv4();    // random id jota kytetn mys linkin luomiseen.
    console.log(viewID);
    console.log(" updaten jlkeen " + latestViewString);
    const response = await fetch('http://localhost:8080/savedviews', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded',
        'Authorization': 'Bearer ' + Token,
      },
        body: `viewID=${viewID}&viewstring=${latestViewString}`
    });

    if (response.status === 200) {
      console.log(response.status);
      setLink(createLink(viewID)); // linkki talteen
      setDisplayShareView(true); // linkkidialogi ikkunan muuttujan toggle
    }
    else {
      console.log(response.status);
    }
  }

  async function updateDefaultViewString() {  // palauttaa stringin handlesaveshareclickille
    try {
      const response = await fetch('http://localhost:8080/users/view', {
        method: 'GET',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded',
          'Authorization': 'Bearer ' + Token,
        },
      });
      const data = await response.text();
      //console.log("Updaten data on " + data);
      return data;
    } catch (error) {
      console.error('Error:', error);
      return null;
    }
  }
  

async function checkDefaultView(){   // tarkistaa onko kyttjll tallennettua nkym ja ohjaa oikealle sivulle.
    fetch('http://localhost:8080/users/view', {
        method: 'GET',  
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded',
            'Authorization': 'Bearer ' + Token,
        },
    })
    .then(response => response.text())
    .then(data => {
        console.log(" Fethin data on " +data);
        if (data.toString() === defaultViewCompareString || data.toString() === defaultViewCompareString2){ 
            navigate("/menu");
        }
        else{
            navigate("/menu/view");
           // setViewString(data.toString());
        }
    })
    .catch((error) => {
        console.error('Error:', error); 
    });
}


useEffect(() => {     // Tarkistaa onko kyttj kirjautunut sisn ja onko jollain muulla sivulla kuin julkisella sivulla, tarvittaessa heitt etusivulle
    if(loggedIn === false && location.pathname !== "/" 
    && location.pathname !== "/showall" 
    && location.pathname !== "/register" 
    && location.pathname !== "/login"
    && !location.pathname.includes("shared")
    ) {      
        console.log("Redirected by browserbar") ;                             
        navigate("/");
    } 
    if (loggedIn === true && location.pathname === "/") {  // jos on kirjautunut sisn, niin heti nkymntarkistus
        checkDefaultView();
    }
}, [loggedIn]);   // tm testaamisen perusteella riitt ett on riippuvainen tst 


const handleLogoutClick = (event) => { //   logout nappulan toiminto
    clearToken();        // tokenin poisto
     // setLoggedIn(false);   // login tilan pivitys, todennkisesti turha koska useEffect tarkistus enivei
    navigate("/");    // todennksesti turhaan tss, koska aiempi useEffect on varmaan jo hoksannut muutoksen ja heittnyt etusivulle
}

const handleShareClick = (event) => {    // share dialogin nappulan toiminto
    handleSaveShareClick();
    setDisplayShareView(displayShareView => !displayShareView);  // toggle displayShareView
  };

const handleShareClose = (event) => {   // share dialogille annettava funktio
    setDisplayShareView(false);
    };

useEffect(() => {
if (loggedIn === false )  {   // nkym ja napit jos ei ole kirjautunut sisn
    setReturnData(
    <div className='logobar'>
        <div className="buttons">
        <Link to ="/"><button data-testid="sign in" className="navbutton">Home</button></Link>
        <Link to ="/login"><button className="navbutton">Log in</button></Link>
        <Link to ="/register"><button className="navbutton">Sign in</button></Link>
        <Link to ="/showall"><button className="navbutton">Show all</button></Link>
        </div>
        </div>) 
    }
    else  {                 // sisnkirjautuneen kyttjn napit, kaksi if ehtoa nytetnk share view ja dialogiehto ja kyytiin open ja onClose sek linkString
        setReturnData(
        <div className='logobar'>
            <div className="buttons">
                <Link to ="/menu"><button className="navbutton">Options</button></Link>
                <button className="navbutton" onClick={handleLogoutClick}>Log out</button>
                {location.pathname==="/menu/view" && (<button className="navbutton" onClick={handleShareClick}>Share view</button>)}
                {location.pathname==="/menu/view" && displayShareView && <AlertDialog open={displayShareView} onClose={handleShareClose} linkString={link} />}
            </div>
        </div>)
    }
}, [loggedIn, setReturnData, location.pathname, displayShareView, link]);    // riippuvainen nist tiloista ja niiden muutoksista

return (
    <div>
        {returnData}
    </div>
    );

};

export default BrowserBar;
